export default {
    $id: "dnd-echart-timeline",
    type: "object",
    title: "timeline",
    default: {},
    properties: {
        show: { type: "boolean", default: true, title: "timeline" },
        type: { type: "string", default: "slider", enum: ["slider"] },
        axisType: { type: "string", default: "time", enum: ["value", "category", "time"], title: "" },
        currentIndex: { type: "number", default: 0, title: "" },
        autoPlay: { type: "boolean", default: true, title: "" },
        rewind: { type: "boolean", default: false, title: "" },
        loop: { type: "boolean", default: true, title: "" },
        playInterval: { type: "number", default: 0, title: "()" },
        realtime: { type: "boolean", default: true, title: "" },
        controlPosition: { type: "string", default: "left", enum: ["left", "right"], title: "" },
        left: {
            type: "string", title: "timeline",
            description: "left  20  '20%'  'left', 'center', 'right'"
        },
        right: {
            type: "string", title: "timeline",
            description: "ottom  20  '20%' "
        },
        top: {
            type: "string", title: "timeline",
            description: " top 'top', 'middle', 'bottom'"
        },
        bottom: {
            type: "string", title: "timeline",
            description: "ottom  20  '20%' "
        },
        padding: { type: "string", title: "", description: "px5" },
        orient: { type: "string", title: "", enum: ["horizontal", "vertical"] },
        inverse: { type: "boolean", default: false, title: " timeline" },
        symbol: {
            type: "string", default: "emptyCircle",
            title: "timeline", enum: ["circle", "rect", "roundRect", "triangle", "diamond", "pin", "arrow"]
        },
        data: {
            type: "array",
            items: {
                type: "object"
            }
        },
        dataDeg: {
            type: "string",
            title: "timeline",
            description: "$"
        }
    }
};



