export default {
    type: "object",
    $id: "dnd-oneof",
    title: "oneofschema",
    default: {},
    required: ["value"],
    properties: {
        type: {
            type: "number",
            enum: [1, 2, 3, 4],
            title: "",
            description: "1:,2:,3:bool,4:object"
        },
        value: {
            oneOf: [{
                $id: "dnd-oneof-number",
                type: "number",
                title: ""
            }, {
                $id: "dnd-oneof-string1",
                type: "string",
                title: ""
            }, {
                $id: "dnd-oneof-boolean",
                type: "boolean",
                title: "bool"
            }, {
                $id: "dnd-oneof-object",
                type: "object",
                title: "object",
                default: {
                },
                required: ["a", "b"],
                properties: {
                    a: { type: "string", default: "nick" },
                    b: { type: "boolean", default: true }
                }
            }]
        }
    }
};



