export default {
    type: "object",
    $id: "dnd-style",
    required: ["textAlign", "width"],
    properties: {
        width: { type: "number", title: "", default: 100 },
        height: { type: "number", equal: "width", title: "", description: "" },
        fontSize: { type: "number" },
        url: { type: "string", format: "url" },
        email: { type: "string", format: "email" },
        ids: {
            type: "array",
            description: "",
            items: {
                title: "ID",
                type: "string",
                format: "uuid"
            }
        },
        textAlign: {
            type: "string",
            enum: ["left", "right", "center"],
            description: "Note: leftrightcenter"
        }
    }
};



