export default {
    type: "object",
    $id: "dnd-echart-itemstyle",
    default: {},
    properties: {
        color: {
            type: "string",
            default: "#ccc",
            title: ""
        },
        lineStyle: {
            $ref: "dnd-echart-linestyle#"
        },
        areaStyle: {
            $ref: "dnd-echart-areastyle#"
        },
        borderColor: {
            type: "string",
            title: ""
        }
    }
};



