
export default {
    type: "object",
    $id: "dnd-echart-tooltip",
    title: "",
    default: {},
    properties: {
        show: { type: "boolean", default: true, title: "" },
        zlevel: { type: "number", default: 0, title: " zlevel " },
        z: { type: "number", default: 6, title: "z", description: "canvaszlevelz" },
        showContent: { type: "boolean", default: true, title: "tooltip" },
        trigger: {
            type: "string", default: "item", enum: ["item", "axis"],
            title: "'item' | 'axis'"
        },
        showDelay: { type: "number", default: 20, title: "ms" },
        hideDelay: { type: "number", default: 100, title: "ms" },
        transitionDuration: {
            type: "number", default: 0.4, title: "s",
            description: "tooltipshowDelay0transitionDuration0"
        },
        enterable: { type: "boolean", default: false, title: "falsetrue" },
        backgroundColor: { type: "string", default: "transparent", title: "", description: "" },
        borderColor: { type: "string", default: "#ccc", title: "", description: " backgroundColor" },
        borderWidth: { type: "number", default: 2, title: "" },
        borderRadius: { type: "number", default: 4, title: "px4" },
        padding: { type: "string", title: "", description: "px5" },
        textStyle: {
            type: "object",
            $ref: "dnd-echart-textstyle#"
        },
        axisPointer: {
            type: "object",
            title: "",
            properties: {
                type: {
                    type: "string",
                    enum: ["line", "cross", "shadow", "none"]
                },
                lineStyle: {
                    type: "object",
                    $ref: "dnd-echart-linestyle#"
                },
                crossStyle: {
                    type: "object",
                    $ref: "dnd-echart-linestyle#"
                },
                shadowStyle: {
                    type: "object",
                    $ref: "dnd-echart-areastyle#"
                }
            }
        }
    }
};



