
export default {
    $id: "dnd-echart-axis",
    title: "axis",
    type: "array",
    default: [],
    items: {
        type: "object",
        default: {},
        properties: {
            zlevel: { type: "number", default: 0, title: " zlevel " },
            z: { type: "number", default: 6, title: "z", description: "canvaszlevelz" },

            show: { type: "boolean", default: true, title: "" },
            type: {
                type: "string",
                default: "",
                title: "",
                description: "'category''value'",
                enum: ["category", "value", "time", "log"]
            },
            position: {
                type: "string",
                title: "",
                description: "'bottom''left''bottom' | 'top' | 'left' | 'right'",
                enum: ["bottom", "left"]
            },
            name: { type: "string", default: "", title: "" },
            nameLocation: {
                type: "string",
                default: "start",
                title: "",
                description: "'end''start' | 'end'",
                enum: ["start", "end"]
            },
            boundaryGap: { type: "boolean", default: true, title: "" },
            min: {
                type: "number"
            },
            max: {
                type: "number"
            },
            scale: {
                type: "boolean",
                default: false,
                title: "0_min_max"
            },
            data: {
                type: "array",
                title: "",
                description: "['1','2']",
                items: {
                    type: "string"
                }
            },
            dataDeg: {
                type: "string",
                title: "",
                description: "$"
            }
        }
    }
};



