export default {
    type: "object",
    $id: "dnd-tree",
    title: "",
    properties: {
        root: {
            type: "object",
            properties: {
                value: {
                    type: "string"
                },
                leftNode: {
                    $ref: "dnd-tree#/properties/root"
                },
                rightNode: {
                    $ref: "dnd-tree#/properties/root"
                }
            }
        }
    }
};



