export default {
    $id: "dnd-echart2",
    type: "object",
    properties: {
        className: { type: "string" },
        notMerge: {
            type: "boolean"
        },
        lazyUpdate: {
            type: "boolean"
        },
        theme: { type: "string" },
        option: {
            type: "object",
            properties: {
                baseOption: {
                    type: "object",
                    $ref: "dnd-echart#"
                },
                options: {
                    type: "array",
                    items: {
                        type: "object",
                        $ref: "dnd-echart#"
                    }
                },
                optionsDeg: {
                    type: "string",
                    title: "timeline",
                    description: "$"
                }
            }

        }
    }
};



