export default {
    type: "object",
    $id: "dnd-common",
    properties: {
        className: {
            type: "string",
            title: "",
            description: ":h-{10-100};:w-{10-100};:ba b--dasbed b--solid;"
        },
        style: {
            type: "object",
            properties: {
                width: { type: "string", title: "" },
                height: { type: "string", title: "" }
            }
        },
        deg: {
            type: "string",
            description: "${{$.test.dataCol}}$sum,$count,$average,$max,$min{{$min($.test_008.ORDER_COUNT_SUM)}}"
        }
    }
};



