
export default {
    type: "object",
    $id: "dnd-echart-toolbox",
    title: "",
    default: {},
    properties: {
        show: { type: "boolean", default: true, title: "" },
        showTitle: { type: "boolean", default: true, title: "" },
        zlevel: { type: "number", default: 0, title: "" },
        z: { type: "number", default: 6, title: "z" },
        orient: { type: "string", title: "", enum: ["horizontal", "vertical"] },
        x: {
            type: "string", default: "left", title: "",
            description: "'center' | 'left' | 'right' | {number}xpx"
        },
        y: {
            type: "string", default: "top",
            title: "", description: "'top' | 'bottom' | 'center' | {number}ypx"
        },
        backgroundColor: { type: "string", default: "transparent", title: "", description: "" },
        borderColor: { type: "string", default: "#ccc", title: "", description: " backgroundColor" },
        borderWidth: { type: "number", default: 2, title: "" },
        padding: { type: "string", title: "", description: "px5" },
        itemGap: { type: "string", title: "" },
        itemSize: { type: "number", default: 16, title: "iconpx" },

        color: {
            type: "array",
            title: "iconfeaturecolor",
            items: {
                type: "string"
            }
        },
        disableColor: { type: "string", default: "#ddd", title: "" },
        effectiveColor: { type: "string", default: "red", title: "" },
        textStyle: {
            type: "object",
            $ref: "dnd-echart-textstyle#"
        },
        feature: {
            type: "object",
            title: ""
        }
    }
};



