
export default {
    type: "object",
    $id: "dnd-echart-title",
    title: "",
    default: {},
    properties: {
        show: { type: "boolean", default: true, title: "" },
        zlevel: { type: "number", default: 0, title: " zlevel " },
        z: { type: "number", default: 6, title: "z", description: "canvaszlevelz" },

        text: { type: "string", default: "", title: "", description: " \n " },
        link: { type: "string", default: "", title: "" },
        target: { type: "string", default: "blank", title: "", enum: ["self", "blank"] },

        subtext: { type: "string", default: "", title: "", description: " \n " },
        sublink: { type: "string", default: "", title: "" },
        subtarget: { type: "string", default: "blank", title: "", enum: ["self", "blank"] },

        x: {
            type: "string", default: "left", title: "",
            description: "'center' | 'left' | 'right' | {number}xpx"
        },
        y: {
            type: "string", default: "top",
            title: "", description: "'top' | 'bottom' | 'center' | {number}ypx"
        },
        textAlign: {
            type: "string", default: "left",
            title: "", description: "x 'left' | 'right' | 'center'"
        },
        backgroundColor: { type: "string", default: "transparent", title: "", description: "" },
        borderColor: { type: "string", default: "#ccc", title: "", description: " backgroundColor" },
        borderWidth: { type: "number", default: 2, title: "" },

        padding: { type: "string", title: "", description: "px5" },
        itemGap: { type: "string", title: "" },
        textStyle: {
            type: "object",
            $ref: "dnd-echart-textstyle#"
        },
        subtextStyle: {
            type: "object",
            $ref: "dnd-echart-textstyle#"
        }
    }
};



