
export default {
    type: "array",
    $id: "dnd-echart-series",
    title: "series",
    default: [],
    items: {
        type: "object",
        default: {},
        properties: {
            zlevel: { type: "number", default: 0, title: " zlevel " },
            z: { type: "number", default: 6, title: "z", description: "canvaszlevelz" },
            type: {
                type: "string",
                default: "line",
                title: "",
                description: " 'line' | 'bar' | 'scatter' | 'k'K | 'pie' | 'radar' | 'chord' | 'force' | 'map'",
                enum: ["line", "bar", "scatter", "pie", "map", "radar", "chord", "force"]
            },
            name: { type: "string", default: "", title: "", description: "legendlegend.data" },
            clickable: { type: "boolean", default: true, title: "click" },
            selectedMode: {
                type: "string",
                default: "single",
                title: "",
                enum: ["single", "multiple"],
                description: "singlemultiple"
            },
            label: {
                type: "object",
                properties: {
                    normal: {
                        type: "object",
                        properties: {
                            show: { type: "boolean" },
                            position: {
                                type: "string",
                                enum: [
                                    "top",
                                    "left",
                                    "right",
                                    "bottom",
                                    "inside",
                                    "insideLeft",
                                    "insideRight",
                                    "insideTop",
                                    "insideBottom",
                                    "insideTopLeft",
                                    "insideBottomLeft",
                                    "insideTopRight",
                                    "insideBottomRight"]
                            },
                            formatter: { type: "string" }
                        }
                    }
                }
            },
            itemStyle: {
                $ref: "dnd-echart-itemstyle#"
            },
            color: {
                type: "array",
                title: "",
                items: {
                    type: "string"
                }
            },
            data: {
                type: "array",
                default: []
            },
            dataDeg: {
                type: "string",
                title: "",
                description: "$"
            }
        }
    }
};



