export default {
    type: "object",
    $id: "dnd-col",
    properties: {
        className: {
            $ref: "dnd-common#/properties/className"
        },
        style: {
            type: "object",
            $ref: "dnd-common#/properties/style"
        },
        small: {
            type: "object",
            title: "",
            description: "320px - 479px)",
            properties: {
                span: { type: "number", default: 1, title: "" },
                pull: { type: "number", default: 1, title: "" },
                push: { type: "number", default: 1, title: "" }
            }
        },
        medium: {
            $ref: "dnd-col#/properties/small",
            type: "object",
            title: "",
            description: "(480px - 639px)"
        },
        large: {
            $ref: "dnd-col#/properties/small",
            type: "object",
            title: "",
            description: "(640px - 1023px)"
        },
        eLarge: {
            $ref: "dnd-col#/properties/small",
            type: "object",
            title: "",
            description: "(1024px - 1365px)"
        },
        eeLarge: {
            $ref: "dnd-col#/properties/small",
            type: "object",
            title: "",
            description: "(1366px - 1919px)"
        },
        eeeLarge: {
            $ref: "dnd-col#/properties/small",
            type: "object",
            title: "",
            description: "(1920px and up)"
        },
    }
};



