export default {
    type: "object",
    $id: "dnd-echart-textstyle",
    default: {},
    properties: {
        color: { type: "string", default: "#333", title: "" },
        fontStyle: { type: "string", default: "normal", title: "" },
        fontWeight: { type: "string", default: "normal", title: "" },
        fontFamily: { type: "string", default: "sans-serif", title: "" },
        fontSize: { type: "number", default: 18, title: "" },
        align: { type: "string", title: "", enum: ["left", "center", "right"] },
        verticalAlign: { type: "string", title: "", enum: ["top", "middle", "bottom"] },
        lineHeight: { type: "number", title: "" },
        width: { type: "string", title: "" },
        height: { type: "string", title: "" },
        textBorderColor: { type: "string", default: "transparent", title: "" },
        textBorderWidth: { type: "number", default: 0, title: "" },
        textShadowColor: { type: "string", default: "transparent", title: "" },
        textShadowBlur: { type: "number", title: "" },
        textShadowOffsetX: { type: "number", default: 0, title: " X " },
        textShadowOffsetY: { type: "number", default: 0, title: " Y " }
    }
};



