export default {
    type: "object",
    $id: "dnd-echart-linestyle",
    default:{},
    properties: {
        color: { type: "string", default: "#ccc", title: "" },
        type: {
            type: "string",
            default: "solid",
            enum: ["solid", "dotted", "dashed", "curve", "broken"],
            title: "'solid' | 'dotted' | 'dashed' 'curve' | 'broken'"
        },
        width: {
            type: "number",
            title: ""
        },
        shadowColor: { type: "string", default: "#ccc", title: "" },
        shadowBlur: {
            type: "number",
            default: 5,
            title: "(IE8+)0"
        },
        shadowOffsetX: {
            type: "number",
            default: 3,
            title: "(IE8+)"
        },
        shadowOffsetY: {
            type: "number",
            default: 3,
            title: "(IE8+)"
        },
    }
};



